import * as React from "react";

interface DemographicsProps {
  onClick: (data: any) => void;
}

interface DemographicsState {
  [index: string]: string;
}

export default class Demographics extends React.Component<DemographicsProps, DemographicsState> {
  constructor(props: DemographicsProps) {
    super(props);
    this.onChange = this.onChange.bind(this);
    this.onClick = this.onClick.bind(this);
    this.state = {
      age: "",
      computer: undefined,
      education: "N",
      gender: "N",
      occupation: "",
      viz: undefined,
    };
  }

  onChange(event: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) {
    this.setState({ [event.target.name]: event.target.value });
  }

  onClick() {
    this.props.onClick(this.state);
  }

  render() {
    const { age, computer, education, gender, occupation, viz } = this.state;

    return (
      <div>
        <h3>Demographic Survey</h3>
        <p>Thank you for participating in our research study. Before proceeding, we ask you to fill out this brief survey to help us better understand the background of our participants. Completion of this survey is optional, and you may proceed at any time by clicking the "Continue" button below. Your individual response to this survey will not be reported, but rather anonymized and aggregated together with the rest of the participant population.</p>
        <div className="question-wrapper">
          <label htmlFor="age"><strong>1.</strong> Your Age:</label>
          <input type="text" id="age" name="age" value={age} onChange={this.onChange}/>
        </div>
        <div className="question-wrapper">
          <label htmlFor="gender"><strong>2.</strong> Your Gender:</label>
          <select id="gender" name="gender" value={gender} onChange={this.onChange}>
            <option value="N">Decline to state</option>
            <option value="M">Male</option>
            <option value="F">Female</option>
            <option value="O">Other</option>
          </select>
        </div>
        <div className="question-wrapper">
          <label htmlFor="education"><strong>3.</strong> What is the highest educational degree you have earned?:</label>
          <select id="education" name="education" value={education} onChange={this.onChange}>
            <option value="N">Decline to state</option>
            <option value="Middle">Middle School Diploma</option>
            <option value="High">High School Diploma</option>
            <option value="Bachelors">Bachelor's Degree</option>
            <option value="Masters">Master's Degree</option>
            <option value="Doctorate">Doctorate Degree</option>
          </select>
        </div>
        <div className="question-wrapper">
          <label htmlFor="occupation"><strong>4.</strong> Your Occupation:</label>
          <input type="text" id="occupation" name="occupation" value={occupation} onChange={this.onChange}/>
        </div>
        <div className="question-wrapper">
          <label htmlFor="computer"><strong>5.</strong> How often do you use a computer on a typical week?:</label>
          <div className="radio-button-wrapper">
            <label><input type="radio" name="computer" value="1" checked={computer === "1"} onChange={this.onChange}/> Never</label>
            <label><input type="radio" name="computer" value="2" checked={computer === "2"} onChange={this.onChange}/> Rarely</label>
            <label><input type="radio" name="computer" value="3" checked={computer === "3"} onChange={this.onChange}/> Sometimes</label>
            <label><input type="radio" name="computer" value="4" checked={computer === "4"} onChange={this.onChange}/> Often</label>
            <label><input type="radio" name="computer" value="5" checked={computer === "5"} onChange={this.onChange}/> Always</label>
          </div>
        </div>
        <div className="question-wrapper">
          <label htmlFor="viz"><strong>6.</strong> How often do you interact with data visualizations on a typical week? (e.g. tables, charts, graphs):</label>
          <div className="radio-button-wrapper">
            <label><input type="radio" name="viz" value="1" checked={viz === "1"} onChange={this.onChange}/> Never</label>
            <label><input type="radio" name="viz" value="2" checked={viz === "2"} onChange={this.onChange}/> Rarely</label>
            <label><input type="radio" name="viz" value="3" checked={viz === "3"} onChange={this.onChange}/> Sometimes</label>
            <label><input type="radio" name="viz" value="4" checked={viz === "4"} onChange={this.onChange}/> Often</label>
            <label><input type="radio" name="viz" value="5" checked={viz === "5"} onChange={this.onChange}/> Always</label>
          </div>
        </div>
        <button className="general-btn exp-nav-btn" onClick={this.onClick}>Complete HIT</button>
      </div>
    );
  }
}



