import * as React from "react";

export const TaskIntro: React.StatelessComponent<{onClick: () => void}> = (p) => {
  return <>
    <h2>You Are Now Ready For Tasks!</h2>
    <p>
      You will now be viewing an interactive visualization over the same data but from the second half of the month.
      Again, we remind you to try to complete the task as quickly and as accurately as possible.
    </p>
    <p>
      There will be three tasks total. Good luck!
    </p>
    <button className="general-btn exp-nav-btn" onClick={p.onClick}>Start Tasks</button>
  </>;
};


